@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;700&display=swap);
body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
      font-family: 'Roboto Slab', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/** @format */
:root,
* {
	box-sizing: border-box;
	/* padding: 0;
	margin: 0; */
}
.app-container {
	box-sizing: border-box;
	background-color: #444;
	overflow-y: hidden;
	overflow-x: hidden;
}

/** @format */

.nav-container {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.title {
	color: goldenrod;
}
.nav-elements {
	flex-basis: 320px;
	padding-left: 0;
	display: flex;
	justify-content: space-between;
	list-style: none;
	text-decoration: none;
	z-index: 10;
}
.nav-elements li {
	color: #ddd;
}
.link {
	text-decoration: none;
}
.burger {
	cursor: pointer;
	display: flex;
	flex-direction: column;
	visibility: hidden;
}
.burger div {
	border-radius: 10px;
	margin-top: 6px;
	width: 35px;
	height: 3px;
	background-color: #ddd;
}

@media screen and (max-width: 550px) {
	.nav-container {
		flex-wrap: wrap;
	}
	.title {
		width: 80%;
		text-align: left;
		padding-left: 1rem;
	}

	.nav-elements {
		background-color: #222;
		flex-direction: column;
		flex: 2 1;
		order: 1;
	}
	.nav-elements li {
		padding: 1rem;
	}
	.nav-elements li:hover {
		background-color: #444;
		color: goldenrod;
		border-color: #ddd;
	}
	.burger {
		visibility: visible;
		padding-right: 1rem;
	}
	.hide {
		display: none;
	}
}

/** @format */
.home-container {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	height: calc(100vh - 72px);
}
.dp {
	max-width: 250px;
	border-radius: 50%;
	border: 3px solid #ddd;
	/* padding: 0.5rem; */
	box-shadow: 2px 2px 3px #ddd;
}
.dp:hover {
	border-color: goldenrod;
	box-shadow: 2px 2px 3px;
}
.home-info {
	color: #ddd;
	font-family: 'Courier New', Courier, monospace;
	text-shadow: 2px 2px 2px #222;
}
.home-info-title {
	font-size: 4rem;
	letter-spacing: 1px;
	margin-bottom: 1rem;
	margin-top: 1rem;
}
.bold {
	color: goldenrod;
	letter-spacing: 1px;
}
.cv-btn {
	cursor: pointer;
	color: goldenrod;
	background: none;
	padding: 0.5rem 1rem;
	font-size: 1rem;
	letter-spacing: 1px;
	font-weight: 700;
	font-family: monospace;
}
.cv-btn:hover {
	color: #ddd;
	border-color: goldenrod;
}
@media screen and (max-width: 850px) {
	.home-container {
		flex-direction: column;
	}
	.home-info {
		padding: 1rem;
	}
	.home-info-title {
		font-size: 3rem;
	}
	.dp {
		z-index: 0;
	}
}

/** @format */

.experience-container {
	max-width: 70%;
	margin: auto;
	/* margin-top: 5rem; */
	color: #ddd;
	height: calc(100vh - 72px);
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.exp-title {
	/* width: 100%; */
}
.exp-sub-container {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
}
.techs {
	padding: 0.5rem;
	background-color: goldenrod;
	flex-basis: 500px;
	border-radius: 5px;
	box-shadow: 2px 2px 2px 2px #333;
	margin-right: 5rem;
}
.techs h2 {
	color: #444;
	padding-left: 1rem;
}
.icon-container {
	width: 350px;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding-bottom: 2rem;
}
.icon {
	font-size: 2.5em;
	color: #333;
	margin: 0.5rem;
	margin-bottom: 1.5rem;
}
.experience-info {
	position: relative;
	padding: 0 0 0 1rem;
	line-height: 1.5;
	font-family: monospace;
	font-size: 1rem;
}
.experience-info::before {
	content: '';
	border-left: 2px solid brown;
	position: absolute;
	left: 0;
	top: 10px;
	bottom: 10px;
	box-shadow: 2px 2px 2px #333;
}
@media screen and (max-width: 850px) {
	.experience-container {
		/* margin-top: 4rem; */
	}
	.exp-sub-container {
		flex-direction: column;
	}
	.techs {
		flex-basis: 200px;
		margin-right: 0;
	}
	.experience-info {
		padding-top: 2rem;
		width: 365px;
		padding-left: 0;
	}
	.experience-info::before {
		border-left: none;
	}
}

/** @format */

.projects-container {
	max-width: 70%;
	margin: auto;
	height: calc(100vh - 72px);
	overflow-x: hidden;
	margin-top: 2rem;
}
.projects-frame-container {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}
.page-title {
	color: #ddd;
	padding-left: 3.5rem;
}
.frame {
	overflow: hidden;
	border-radius: 10px;
	border: none;
	box-shadow: 2px 2px 2px 2px;
	margin: 1rem;
}
.ref {
	display: block;
	padding: 1rem;
	text-decoration: none;
	color: goldenrod;
	text-align: center;
	text-shadow: 2px 2px 2px #222;
}
.ref:hover {
	color: #ddd;
}
@media screen and (max-width: 550px) {
	.projects-container {
		max-width: 100%;
	}
}

/** @format */

.skill-container {
	height: calc(100vh - 72px);
	background-color: #444;
	color: #ddd;
	box-sizing: border-box;
}
.meter-container {
	border-radius: 10px;
	box-shadow: 2px 2px 2px 2px #333;
	height: 400px;
	max-width: 70%;
	margin: auto;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
}
.skills-title {
	text-align: center;
}
.meter {
	padding: 0.3rem;
	max-width: 400px;
	border: 2px solid #ddd;
	border-radius: 10px;
	display: flex;
	justify-content: space-between;
	margin: 1rem;
	text-align: left;
}
#meter {
	width: 200px;
}
label {
	color: goldenrod;
}
@media screen and (max-width: 950px) {
	.meter-container {
		max-width: 90%;
		height: auto;
		padding-bottom: 1rem;
	}
	.meter {
		margin: auto;
		margin-top: 1rem;
		width: 300px;
	}
}

